<template>
  <transition appear name="up-slide-fade" :duration="{ enter: 1000, leave: 800 }">
    <v-timeline height=100% v-if="timeline" :dense="small">
      <transition-group name="list" tag="p">
        <v-timeline-item v-show="timeline" v-for="entry in milestones" :key="entry.title" color="rgb(208,158,56)"
          :large="!small" :icon="entry.icon">
          <template v-slot:opposite>
            <span>{{ entry.date }}</span>
          </template>
          <v-card class="elevation-2" v-show="timeline">
            <v-card-title v-show="!isMobile" class="headline">
              {{ entry.title }}
            </v-card-title>
            <v-card-text v-show="!isMobile">
              {{ entry.desc }}
            </v-card-text>
            <v-card-text v-show="isMobile">
              {{ entry.title }}
            </v-card-text>
          </v-card>
        </v-timeline-item>
      </transition-group>
    </v-timeline>
  </transition>
</template>

<script>
export default {
  components: {

  },
  computed: {
    small() {
      switch (this.$vuetify.breakpoint.name) {
        case 'xs': return true
        case 'sm': return true
        case 'md': return true
        case 'lg': return false
        case 'xl': return false
        default: return true
      }
    }
  },
  data: () => ({
    isMobile: false,
    timeline: false,
    milestones: [
    {
        icon: 'mdi-briefcase',
        date: 'Apr. 2024 ~ Present',
        title: 'NeoBards - Technical Designer',
        desc: 'Contibuting to design work with a focus on technical realization at NeoBards Breda.'
      },
      {
        icon: 'mdi-briefcase',
        date: 'Sept. 2023 ~ Jun. 2024',
        title: 'TU/e - Hybrid Teacher',
        desc: 'Supporting the teaching staff through industry expertise in the Design for Games and Play squad at the faculty of Industrial Design.'
      },
      {
        icon: 'mdi-briefcase',
        date: 'March. 2021 ~ March. 2024',
        title: 'Enversed Studios - Project Lead',
        desc: 'Leading in the design and development of multiplayer VR experiences for industry and entertainment. Developing frameworks, distribution solutions, service ecosystems, and new technologies.'
      },
      {
        icon: 'mdi-school',
        date: 'Sept. 2018 ~ Jan. 2021',
        title: 'TU/e - Master Industrial Design',
        desc: 'Graduated from the Eindhoven University of Technology as a Master Industrial Design with expertise in Augment Reality, Virtual Reality, Mixed Reality Design, Transformative Practises, User Experience Design and Serious Games.'
      },
      {
        icon: 'mdi-briefcase',
        date: 'Sept. 2019 ~ Jan. 2020',
        title: 'TNO - Intern',
        desc: 'Researching note-taking interactions in virtual reality business meetings with higly immersive physical presense.'
      },
      {
        icon: 'mdi-briefcase',
        date: 'Sept. 2018 ~ Jan. 2021',
        title: 'TU/e - Student Assistant',
        desc: 'Grading and/ or tutoring for all Design for Games and Play courses (3 courses in 1 package), as well as tutoring for Engineering Design.'
      },
      {
        icon: 'mdi-school',
        date: 'Sept. 2007 ~ Jul. 2014',
        title: 'TU/e - Bachelor Industrial Design',
        desc: 'Graduated from the Eindhoven University of Technology as a Master Industrial Design with expertise in Augment Reality, Virtual Reality and Serious Games.'
      }
    ],
    ticks: 0
  }),
  methods: {

  },
  async mounted() {
    // Monitor mobile page toggle
    this.$root.$on('onMobileToggle', (isMobile) => {
      console.log(`toggled mobile: ${isMobile}`)
      this.isMobile = isMobile
    })

    // Animate Timeline 
    if (!this.timeline) {
      setTimeout((instance) => {
        instance.timeline = true
      }, 600, this)
    }

  },
  beforeDestroy: () => {

  }
}
</script>
<style scoped>
li {
  list-style: none;
  margin-left: 0;
  margin-right: 100px;
  overflow: hidden;
}

li div {
  margin: 0.5em 0 0;
  border: 0.1em solid #6e531a;
  border-radius: 3px;
  background: #e8d09f;
  box-shadow: 0 2px 1px -1px rgb(0 0 0 / 20%), 0 1px 1px 0 rgb(0 0 0 / 14%), 0 1px 3px 0 rgb(0 0 0 / 12%);
}

li div {
  position: relative;
  height: 1.4em;
  background: #d09e38;
}

li div label:first-child {
  position: absolute;
  color: darkslategray;
  right: 2px;
  top: -1px;
}
</style>
